// Memoize row component for better performance
const Row = memo(({ index, style, data }: { index: number; style: any; data: any }) => {
  const { foils, styles, isSelected, onFoilSelect } = data;
  const foil = foils[index];
  const rowKey = `row-${foil.brand}-${foil.model}`.toLowerCase().replace(/\s+/g, '-');
  const selected = isSelected(foil);
  
  return (
    <Pressable
      key={rowKey}
      onPress={() => onFoilSelect(foil)}
      style={[styles.row, selected && styles.selectedRow, style]}
    >
      <View style={[styles.cell, styles.textCell, styles.brandCell]}>
        <Text style={styles.text}>{foil.brand}</Text>
      </View>
      <View style={[styles.cell, styles.textCell, styles.seriesCell]}>
        <Text style={styles.text}>{foil.series}</Text>
      </View>
      <View style={[styles.cell, styles.textCell, styles.modelCell]}>
        <Text style={styles.text}>{foil.model}</Text>
      </View>
      <View style={[styles.cell, styles.numericCell, styles.wingspanCell]}>
        <Text style={styles.numericText}>{foil.specs.wingspan || '-'}</Text>
      </View>
      <View style={[styles.cell, styles.numericCell, styles.areaCell]}>
        <Text style={styles.numericText}>{foil.specs.area || '-'}</Text>
      </View>
      <View style={[styles.cell, styles.numericCell, styles.arCell]}>
        <Text style={styles.numericText}>{foil.specs.aspectRatio?.toFixed(1) || '-'}</Text>
      </View>
      <View style={[styles.cell, styles.numericCell, styles.cordCell]}>
        <Text style={styles.numericText}>{foil.specs.cord || '-'}</Text>
      </View>
      <View style={[styles.cell, styles.textCell, styles.typeCell]}>
        <Text style={styles.text}>{foil.type}</Text>
      </View>
      <View style={[styles.cell, styles.numericCell, styles.priceCell]}>
        <Text style={styles.numericText}>{foil.price ? `$${foil.price}` : '-'}</Text>
      </View>
      <View style={[styles.cell, styles.textCell, styles.linkCell]}>
        <Text 
          style={[styles.text, styles.link]}
          onPress={() => Platform.OS === 'web' ? window.open(foil.sourceUrl, '_blank') : null}
        >
          View
        </Text>
      </View>
    </Pressable>
  );
});

Row.displayName = 'Row';

// Memoize header component
const TableHeader = memo(({ onSort, sortOption, styles, columnWidths }: any) => {
  const renderHeaderContent = (text: string, field: SortField, isNumeric: boolean = false) => {
    const isActive = sortOption.field === field;
    return (
      <View style={isNumeric ? styles.headerNumericContent : styles.headerContent}>
        <Text style={[styles.headerText, isNumeric && styles.numericText]}>{text}</Text>
        <View style={styles.sortIndicatorContainer}>
          <Text style={[
            styles.sortArrow,
            isActive && sortOption.direction === 'asc' ? styles.sortArrowActive : styles.sortArrowInactive
          ]}>▲</Text>
          <Text style={[
            styles.sortArrow,
            isActive && sortOption.direction === 'desc' ? styles.sortArrowActive : styles.sortArrowInactive
          ]}>▼</Text>
        </View>
      </View>
    );
  };

  return (
    <View style={styles.headerRow}>
      <Pressable style={[styles.cell, styles.headerCell, styles.brandCell]} onPress={() => onSort('brand')}>
        {renderHeaderContent('Brand', 'brand')}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.seriesCell]} onPress={() => onSort('series')}>
        {renderHeaderContent('Series', 'series')}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.modelCell]} onPress={() => onSort('model')}>
        {renderHeaderContent('Model', 'model')}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.wingspanCell]} onPress={() => onSort('wingspan')}>
        {renderHeaderContent('Wingspan', 'wingspan', true)}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.areaCell]} onPress={() => onSort('area')}>
        {renderHeaderContent('Area', 'area', true)}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.arCell]} onPress={() => onSort('aspectRatio')}>
        {renderHeaderContent('AR', 'aspectRatio', true)}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.cordCell]} onPress={() => onSort('cord')}>
        {renderHeaderContent('Cord', 'cord', true)}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.typeCell]} onPress={() => onSort('type')}>
        {renderHeaderContent('Type', 'type')}
      </Pressable>
      <Pressable style={[styles.cell, styles.headerCell, styles.priceCell]} onPress={() => onSort('price')}>
        {renderHeaderContent('Price', 'price', true)}
      </Pressable>
      <View style={[styles.cell, styles.headerCell, styles.linkCell]}>
        {renderHeaderContent('Product Link', 'link')}
      </View>
    </View>
  );
});

TableHeader.displayName = 'TableHeader';

const FoilsTable = ({ foils, onSort, onFoilSelect, isSelected, sortOption }: FoilsTableProps) => {
  const columnWidths = useColumnWidths(foils);
  const styles = useStyles(columnWidths);
  const { height: windowHeight } = useWindowDimensions();
  const [tableHeight, setTableHeight] = useState(500);
  const [headerHeight, setHeaderHeight] = useState(50);

  useEffect(() => {
    if (Platform.OS === 'web') {
      const calculateHeight = () => {
        const footerHeight = 60;
        const filterHeight = 60;
        const padding = 40;
        const availableHeight = windowHeight - footerHeight - filterHeight - headerHeight - padding;
        setTableHeight(Math.max(400, availableHeight));
      };

      calculateHeight();
      window.addEventListener('resize', calculateHeight);
      return () => window.removeEventListener('resize', calculateHeight);
    }
  }, [windowHeight, headerHeight]);

  const getTableWidth = () => {
    return Object.values(columnWidths).reduce((sum, width) => sum + width, 0);
  };

  const itemData = useMemo(() => ({
    foils,
    styles,
    isSelected,
    onFoilSelect
  }), [foils, styles, isSelected, onFoilSelect]);

  return (
    <View style={styles.container}>
      <ScrollView horizontal style={styles.horizontalScrollView} contentContainerStyle={styles.contentContainer}>
        <View style={{ width: getTableWidth() }}>
          <View 
            style={styles.headerContainer}
            onLayout={(event) => {
              const { height } = event.nativeEvent.layout;
              setHeaderHeight(height);
            }}
          >
            <TableHeader
              onSort={onSort}
              sortOption={sortOption}
              styles={styles}
              columnWidths={columnWidths}
            />
          </View>
          {Platform.OS === 'web' ? (
            <View style={styles.virtualizedWrapper}>
              <List
                height={tableHeight}
                itemCount={foils.length}
                itemSize={50}
                width={getTableWidth()}
                itemData={itemData}
              >
                {Row}
              </List>
            </View>
          ) : (
            <View style={styles.tableWrapper}>
              {foils.map((foil, index) => (
                <Row
                  key={`${foil.brand}-${foil.model}`}
                  index={index}
                  style={{}}
                  data={itemData}
                />
              ))}
            </View>
          )}
        </View>
      </ScrollView>
    </View>
  );
};

export default memo(FoilsTable);