import React from 'react';
import { View, StyleSheet } from 'react-native';
import { FilterPanel } from './FilterPanel';
import { FilterOptions } from '../types/filters';

interface FilterControlsProps {
  filterOptions: {
    brands: string[];
    series: string[];
    types: string[];
    brandSeries: Record<string, string[]>;
  };
  selectedBrands: string[];
  selectedSeries: string[];
  selectedTypes: string[];
  onFilterChange: (filterType: keyof FilterOptions, values: string[]) => void;
}

export function FilterControls({
  filterOptions,
  selectedBrands = [],
  selectedSeries = [],
  selectedTypes = [],
  onFilterChange,
}: FilterControlsProps) {
  const handleBrandFilter = (brand: string) => {
    const newBrands = selectedBrands.includes(brand)
      ? selectedBrands.filter(b => b !== brand)
      : [...selectedBrands, brand];
    onFilterChange('brands', newBrands);
  };

  const handleSeriesFilter = (series: string) => {
    const newSeries = selectedSeries.includes(series)
      ? selectedSeries.filter(s => s !== series)
      : [...selectedSeries, series];
    onFilterChange('series', newSeries);
  };

  const handleTypeFilter = (type: 'front' | 'tail' | null) => {
    onFilterChange('type', type ? [type] : []);
  };

  return (
    <View style={styles.container}>
      <FilterPanel
        filters={{
          brands: selectedBrands,
          series: selectedSeries,
          type: selectedTypes.length > 0 ? selectedTypes[0] as 'front' | 'tail' : null,
        }}
        filterOptions={filterOptions}
        onBrandFilter={handleBrandFilter}
        onSeriesFilter={handleSeriesFilter}
        onTypeFilter={handleTypeFilter}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    padding: 8,
  },
}); 