import React, { useState, useRef } from 'react';
import { View, Text, Pressable, StyleSheet, Animated, ScrollView, LayoutChangeEvent } from 'react-native';
import { Ionicons } from '@expo/vector-icons';

interface FilterPanelProps {
  filters: {
    brands: string[];
    series: string[];
    type: 'front' | 'tail' | null;
  };
  filterOptions: {
    brands: string[];
    series: string[];
    brandSeries: Record<string, string[]>;
  };
  onBrandFilter: (brand: string) => void;
  onSeriesFilter: (series: string) => void;
  onTypeFilter: (type: 'front' | 'tail' | null) => void;
}

export function FilterPanel({
  filters,
  filterOptions,
  onBrandFilter,
  onSeriesFilter,
  onTypeFilter,
}: FilterPanelProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [animation] = useState(new Animated.Value(0));
  const [contentHeight, setContentHeight] = useState(0);

  const toggleExpanded = () => {
    const toValue = isExpanded ? 0 : 1;
    Animated.spring(animation, {
      toValue,
      useNativeDriver: false,
    }).start();
    setIsExpanded(!isExpanded);
  };

  const handleLayout = (event: LayoutChangeEvent) => {
    const { height } = event.nativeEvent.layout;
    setContentHeight(height);
  };

  const animatedHeight = animation.interpolate({
    inputRange: [0, 1],
    outputRange: [0, contentHeight],
  });

  return (
    <View style={styles.container}>
      <Pressable onPress={toggleExpanded} style={styles.header}>
        <Text style={styles.headerText}>Filters</Text>
        <Ionicons 
          name={isExpanded ? 'chevron-up' : 'chevron-down'} 
          size={24} 
          color="#666"
        />
      </Pressable>

      <Animated.View style={[styles.content, { height: animatedHeight }]}>
        <View style={styles.scrollContainer} onLayout={handleLayout}>
          <View style={styles.filterSection}>
            <View style={styles.filterRow}>
              <Text style={styles.filterTitle}>Type:</Text>
              <View style={styles.filterButtons}>
                <Pressable
                  style={[
                    styles.filterButton,
                    filters.type === 'front' && styles.filterButtonActive,
                  ]}
                  onPress={() => onTypeFilter(filters.type === 'front' ? null : 'front')}
                >
                  <Text style={[
                    styles.filterButtonText,
                    filters.type === 'front' && styles.filterButtonTextActive,
                  ]}>Front</Text>
                </Pressable>
                <Pressable
                  style={[
                    styles.filterButton,
                    filters.type === 'tail' && styles.filterButtonActive,
                  ]}
                  onPress={() => onTypeFilter(filters.type === 'tail' ? null : 'tail')}
                >
                  <Text style={[
                    styles.filterButtonText,
                    filters.type === 'tail' && styles.filterButtonTextActive,
                  ]}>Tail</Text>
                </Pressable>
              </View>
            </View>
          </View>

          <View style={styles.filterSection}>
            <View style={styles.filterRow}>
              <Text style={styles.filterTitle}>Brand:</Text>
              <View style={styles.filterButtons}>
                {filterOptions.brands.map(brand => (
                  <Pressable
                    key={brand}
                    style={[
                      styles.filterButton,
                      filters.brands.includes(brand) && styles.filterButtonActive,
                    ]}
                    onPress={() => onBrandFilter(brand)}
                  >
                    <Text style={[
                      styles.filterButtonText,
                      filters.brands.includes(brand) && styles.filterButtonTextActive,
                    ]}>{brand}</Text>
                  </Pressable>
                ))}
              </View>
            </View>
          </View>

          <View style={styles.filterSection}>
            <View style={styles.filterRow}>
              <Text style={styles.filterTitle}>Series:</Text>
              <View style={styles.filterButtons}>
                {filterOptions.series.map(series => (
                  <Pressable
                    key={series}
                    style={[
                      styles.filterButton,
                      filters.series.includes(series) && styles.filterButtonActive,
                    ]}
                    onPress={() => onSeriesFilter(series)}
                  >
                    <Text style={[
                      styles.filterButtonText,
                      filters.series.includes(series) && styles.filterButtonTextActive,
                    ]}>{series}</Text>
                  </Pressable>
                ))}
              </View>
            </View>
          </View>
        </View>
      </Animated.View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: 'white',
    borderBottomWidth: 1,
    borderBottomColor: '#eee',
  },
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 12,
  },
  headerText: {
    fontSize: 16,
    fontWeight: 'bold',
  },
  content: {
    overflow: 'hidden',
    position: 'relative',
  },
  scrollContainer: {
    position: 'absolute',
    left: 0,
    right: 0,
  },
  filterSection: {
    padding: 8,
    borderTopWidth: 1,
    borderTopColor: '#eee',
  },
  filterRow: {
    flexDirection: 'row',
    alignItems: 'flex-start',
  },
  filterTitle: {
    fontSize: 14,
    color: '#666',
    marginRight: 8,
    minWidth: 50,
    paddingTop: 6, // Align with filter buttons
  },
  filterButtons: {
    flex: 1,
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 6,
  },
  filterButton: {
    paddingHorizontal: 12,
    paddingVertical: 6,
    borderRadius: 16,
    backgroundColor: 'white',
    borderWidth: 1,
    borderColor: '#ddd',
    marginBottom: 4,
  },
  filterButtonActive: {
    backgroundColor: '#007AFF',
    borderColor: '#007AFF',
  },
  filterButtonText: {
    color: '#333',
    fontSize: 13,
  },
  filterButtonTextActive: {
    color: 'white',
  },
}); 